













import { Vue, Component, Prop, Watch, Emit } from 'vue-property-decorator'

@Component
export default class Loading extends Vue {
    @Prop(Error) error!: Error
    showSpinner = false

    @Emit('reload')
    emitReload() {}

    mounted() {
        setTimeout(() => {

            this.showSpinner = true

        }, 500)
    }
}
